const NaviText = styled.div`
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 19px;
  align-items: center;
  text-align: center;
`;

enum DirectionType {
  faster = "jam",
  shorter = "shortest",
}

interface DirectionCardProps {
  directionsStore: DirectionsStore;
  mapglStore: MapglStore;
  searchStore: SearchStore;
  currentGeolocation: EnrichedGeolocationCoordinates;
}

export const DirectionCard = observer(
  ({
    directionsStore,
    searchStore,
    mapglStore,
    currentGeolocation,
  }: DirectionCardProps) => {
    useEffect(() => {
      window.history.pushState(
        null,
        "",
        window.location.hostname + "/direction"
      );
    }, []);

    const [selectedDirection, setSelectedDirection] = useState(
      DirectionType.faster
    );

    const [faster, setFaster] = useState<any>(null);
    const [shortest, setShortest] = useState<any>(null);
    const [isNavigatorsShown, setIsNavigatorsShown] = useState(false);

    const buildDirection = (
      endPoint: number[],
      type: string,
      color: string
    ): Promise<any> => {
      return directionsStore.buildDirection(
        [currentGeolocation.longitude, currentGeolocation.latitude],
        endPoint,
        type,
        color
      );
    };

    useEffect(() => {
      searchStore.currentObject &&
        (mapglStore.currentMarkerCoords || searchStore.currentObject.point) &&
        buildDirection(
          mapglStore.currentMarkerCoords || [
            searchStore.currentObject.point.lon,
            searchStore.currentObject.point.lat,
          ],
          selectedDirection,
          selectedDirection === DirectionType.faster ? "#23B064" : "#0090FF"
        ).then((res) => {
          searchStore.currentObject &&
            (mapglStore.currentMarkerCoords ||
              searchStore.currentObject.point) &&
            directionsStore
              .getDirection(
                [currentGeolocation.longitude, currentGeolocation.latitude],
                mapglStore.currentMarkerCoords || [
                  searchStore.currentObject.point.lon,
                  searchStore.currentObject.point.lat,
                ],
                DirectionType.faster
              )
              .then((res) => {
                setFaster(res.data[0]);
              });

          searchStore.currentObject &&
            (mapglStore.currentMarkerCoords ||
              searchStore.currentObject.point) &&
            directionsStore
              .getDirection(
                [currentGeolocation.longitude, currentGeolocation.latitude],
                mapglStore.currentMarkerCoords || [
                  searchStore.currentObject.point.lon,
                  searchStore.currentObject.point.lat,
                ],
                DirectionType.shorter
              )
              .then((res) => {
                setShortest(res.data[0]);
              });
        });
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedDirection, directionsStore, searchStore.currentObject]);

    return (
      <>
        <DirectionCardWrapper>
          <DirectionButtonsWrapper>
            <DirectionButton
              onClick={() => {
                selectedDirection !== DirectionType.faster &&
                  mapglStore.destroyDirection();
                setSelectedDirection(DirectionType.faster);
              }}
              style={
                selectedDirection === DirectionType.faster
                  ? {
                      border: "1px solid  #dafae8",
                      background: "#92c9ab",
                      borderRadius: "3px",
                    }
                  : {}
              }
            >
              <RouteTime>{(shortest?.duration / 60).toFixed(1)} min</RouteTime>
              <div> </div>
              <Distance>{(shortest?.distance / 1000).toFixed(1)} km</Distance>
              <div>shorter </div>
            </DirectionButton>
            <DirectionButton
              onClick={() => {
                selectedDirection !== DirectionType.shorter &&
                  mapglStore.destroyDirection();
                setSelectedDirection(DirectionType.shorter);
              }}
              style={
                selectedDirection === DirectionType.shorter
                  ? {
                      border: "1px solid  #dafae8",
                      background: "#92c9ab",
                      borderRadius: "3px",
                    }
                  : {}
              }
            >
              <RouteTime>{(faster?.duration / 60).toFixed(1)} min</RouteTime>
              <div></div>
              <Distance>{(faster?.distance / 1000).toFixed(1)} km</Distance>
              <div>faster </div>
            </DirectionButton>
          </DirectionButtonsWrapper>
          <LetsGoButton onClick={() => setIsNavigatorsShown(true)}>
            Let`s go
          </LetsGoButton>
          <DirectionsLength>You have 28 routes left</DirectionsLength>
        </DirectionCardWrapper>
        {isNavigatorsShown && (
          <ScreenContainer onClick={() => setIsNavigatorsShown(false)}>
            <NavigatorsContainer>
              <NaviContainer
                onClick={() =>
                  SendData({
                    type: PostMessageTypeEnum.ROUTE,
                    data: {
                      navigator: NavigatorsEnum.TWO_GIS,
                      currentPoint: `${currentGeolocation.latitude},${currentGeolocation.longitude}`,
                      routPoint: `${searchStore.currentObject?.point.lat},${searchStore.currentObject?.point.lon}`,
                    },
                  })
                }
              >
                <TwoGisIcon />
                <NaviText style={{ color: "#9D9D9D" }}>
                  {NavigatorsEnum.TWO_GIS}
                </NaviText>
              </NaviContainer>
              <NaviContainer
                onClick={() =>
                  SendData({
                    type: PostMessageTypeEnum.ROUTE,
                    data: {
                      navigator: NavigatorsEnum.WAZE,
                      currentPoint: `${currentGeolocation.latitude},${currentGeolocation.longitude}`,
                      routPoint: `${searchStore.currentObject?.point.lat},${searchStore.currentObject?.point.lon}`,
                    },
                  })
                }
              >
                <WazeIcon />
                <NaviText>{NavigatorsEnum.WAZE}</NaviText>
              </NaviContainer>
              <NaviContainer
                onClick={() =>
                  SendData({
                    type: PostMessageTypeEnum.ROUTE,
                    data: {
                      navigator: NavigatorsEnum.GOOGLE,
                      currentPoint: `${currentGeolocation.latitude},${currentGeolocation.longitude}`,
                      routPoint: `${searchStore.currentObject?.point.lat},${searchStore.currentObject?.point.lon}`,
                    },
                  })
                }
              >
                <GoogleIcon />
                <NaviText>{NavigatorsEnum.GOOGLE}</NaviText>
              </NaviContainer>
              <NaviContainer
                onClick={() =>
                  SendData({
                    type: PostMessageTypeEnum.ROUTE,
                    data: {
                      navigator: NavigatorsEnum.OSM,
                      currentPoint: `${currentGeolocation.latitude},${currentGeolocation.longitude}`,
                      routPoint: `${searchStore.currentObject?.point.lat},${searchStore.currentObject?.point.lon}`,
                    },
                  })
                }
              >
                <OsmIcon />
                <NaviText>{NavigatorsEnum.OSM}</NaviText>
              </NaviContainer>
            </NavigatorsContainer>
          </ScreenContainer>
        )}
      </>
    );
  }
);
