/* eslint-disable  */
import Burger from "assets/images/burger.svg";
import Search from "assets/images/search.svg";
import BackArrow from "assets/images/back-arrow.svg";
import Cancel from "assets/images/cancel.svg";
import { useState } from "react";
import { SearchStore } from "stores/SearchStore";
import styled from "styled-components";
import { MapglStore } from "stores/MapglStore";
import { ID_TYPES } from "api/search";
import { ResultItem } from "api/interfaces/search.interface";
import mapglTypes from "@2gis/mapgl/types/index";

const SearchInputWrapper = styled.div`
  position: absolute;
  z-index: 2;
  width: 95%;
  height: 48px;
  background: #ffffff;
  border-radius: 30px;
  border: #fffff;
  display: flex;
  margin: 0 auto;
  margin-top: 50px;
  left: 0;
  right: 0;
  margin-top: 50px;
`;

const BurgerWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 40px;
  background: rgba(33, 33, 33, 0.08);
  border-radius: 20px;
  margin: 4px;
`;

const BurgerIcon = styled.div`
  background-image: url(${Burger});
  width: 18px;
  height: 12px;
`;

const SearchIcon = styled.div`
  background-image: url(${Search});
  width: 18px;
  height: 18px;
  margin-left: 7px;
  margin-top: 15px;
`;

const Input = styled.input`
  height: 24px;
  width: 190px;
  margin-left: 27px;
  margin-top: 10px;
  border: 0;
`;

const BackArrowIcon = styled.div`
  background-image: url(${BackArrow});
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  margin-left: 7px;
  margin-top: 15px;
`;

const CancelIcon = styled.div`
  background-image: url(${Cancel});
  width: 14px;
  height: 14px;
  margin-left: 7px;
  margin-top: 17px;
`;

interface SeacrhInputProps {
  searchStore: SearchStore;
  mapglStore: MapglStore;
  setOpenObjectCard: (value: boolean) => void;
  setIsEnterPressed: (value: boolean) => void;
  setIsResultsOpen: (value: boolean) => void;
  searchInputRef: React.RefObject<HTMLInputElement>;
  destroyAll: () => void;
  setIsMarkerCentred: (val: boolean) => void;
  isDirectionCard: boolean;
}

export const SearchInput = ({
  searchStore,
  mapglStore,
  searchInputRef,
  setOpenObjectCard,
  setIsEnterPressed,
  setIsResultsOpen,
  destroyAll,
  setIsMarkerCentred,
  isDirectionCard,
}: SeacrhInputProps) => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isInputFocused, setIsInputFocused] = useState<boolean>(false);
  const [inputValue, setInputValue] = useState("");

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    if (searchInputRef.current?.value) {
      searchInputRef.current && setInputValue(searchInputRef.current?.value);
      searchStore.setSearchValue(searchInputRef.current?.value);
    }
    const viewPoints = mapglStore.mapgl?.getBounds();

    searchStore.getSuggestions(
      event.target.value,
      viewPoints?.northEast as [number, number],
      viewPoints?.southWest as [number, number]
    );
  };

  const handleEnter = async (item: any) => {
    if (searchInputRef.current) {
      searchInputRef.current.value = item.name;
    }

    if (
      item.type === "rubric" ||
      item.type === "metarubric" ||
      item.type === "org"
    ) {
      const reqParams = {
        type: "id",
        params: {
          idType: ID_TYPES[item.type],
          id: item.id,
        },
      };

      searchStore.setReqParams(reqParams as any);

      const viewPoints = mapglStore.mapgl?.getBounds();

      const res = await searchStore.getSearchResults(
        reqParams.params.idType,
        reqParams.params.id,
        viewPoints?.northEast as [number, number],
        viewPoints?.southWest as [number, number],
        50
      );

      res.map((item: any) => {
        if (item.point) {
          const marker = mapglStore.buildMarker([
            item.point.lon,
            item.point.lat,
          ]);
          marker?.on("click", () => {
            createMarkerEvent(marker, item);
          });
        }
      });
      setIsResultsOpen(false);
      // TODO fix types
    } else {
      const res: any = await searchStore.getObjectById(item.id);
      const resultItem: ResultItem = res.result?.items[0];

      searchStore.setCurrentObject(resultItem);
      mapglStore.buildMarker([resultItem.point.lon, resultItem.point.lat]);
      mapglStore.setCenter([resultItem.point.lon, resultItem.point.lat]);
      setOpenObjectCard(true);
    }
  };

  const createMarkerEvent = (marker: mapglTypes.Marker, item: ResultItem) => {
    if (!isDirectionCard) {
      mapglStore.markers.map((marker: mapglTypes.Marker) => {
        marker.setIcon({ icon: "./marker.svg" });
      });
      mapglStore.setCurrentMarker(marker);
      marker.setIcon({ icon: "./red-marker.svg" });
      searchStore.setCurrentObject(item);
      setOpenObjectCard(true);
      mapglStore.setCurrentMarkerCoords([item.point.lon, item.point.lat]);
      mapglStore.setCenter([item.point.lon, item.point.lat]);
    }
  };

  return (
    <SearchInputWrapper
      onKeyDown={(event) => {
        if (event.key === "Enter") {
          window.history.pushState(
            null,
            "",
            window.location.hostname + "/results"
          );
          searchStore.resetReqParams();
          if (searchInputRef.current?.value) {
            setIsMarkerCentred(false);
            destroyAll();
            searchStore.suggestionsResults.map((item: any, index) => {
              handleEnter(item);
            });
          }
        } else {
          setIsEnterPressed(false);
        }
      }}
      style={{
        justifyContent: inputValue ? "space-around" : "",
      }}
      onClick={() => {
        searchInputRef.current && searchInputRef.current.focus();
      }}
    >
      {inputValue === "" && (
        <BurgerWrapper>
          <BurgerIcon></BurgerIcon>
        </BurgerWrapper>
      )}
      {inputValue === "" && <SearchIcon></SearchIcon>}
      <div style={{ display: "flex" }}>
        {inputValue && (
          <BackArrowIcon
            onClick={() => {
              if (searchInputRef?.current?.value && searchStore.searchValue) {
                searchInputRef.current.value = searchStore.searchValue;
              }
              setOpenObjectCard(false);
            }}
          />
        )}
        <Input
          placeholder="Search"
          onChange={handleChange}
          ref={searchInputRef}
          onFocus={() => {
            setIsInputFocused(true);
            setIsResultsOpen(true);
            setOpenObjectCard(false);
            setIsEnterPressed(false);
            destroyAll();
          }}
          style={{
            marginLeft: inputValue ? "24px" : "27px",
          }}
          onBlur={() => setIsInputFocused(false)}
        ></Input>
      </div>
      {inputValue && (
        <CancelIcon
          onClick={() => {
            if (searchInputRef.current?.value) {
              searchInputRef.current.value = "";
              destroyAll();
              setOpenObjectCard(false);
              setIsResultsOpen(false);
            }
          }}
        />
      )}
    </SearchInputWrapper>
  );
};
